@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav > .images > img{
  width: 25px;
  height: 25px;
}
nav > .images > img:hover{
  cursor: pointer;
}

.group > img{
  width: 35px;
  height: 35px;  
}
span > img:hover{
  cursor: pointer;
}
.group > img:hover{
  cursor: pointer;
}
.group{
  display: flex;
  align-items: center;
}
span > img{
  width: 35px;
  height: 35px;
}
nav{
  margin-top: 1vh;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.left{
  padding: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width:15%;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  font-size: larger;
}
.Middle{
  display: flex;
  align-items: center;
  width: 45%;
}
.input{
  width:100% ;
  height: 35px;
  border-radius:25px 0px 0px 25px;
  border-color: rgba(128, 128, 128, 0.2);
}
.Middle > span > img{
  margin-top: 4px;
  border: 2px solid;
  border-color: lightgrey;
  height: 25px;
  width: 25px;
  padding: 6px;
  background-color:rgba(128, 128, 128, 0.1);
  border-radius:0px 25px 25px 0px;
}
.right{
  width:15%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.right > span{
  font-family: Arial, Helvetica, sans-serif;
  font-size: x-large;
  border: 2px solid;
  padding: 5%;
  border-radius: 25px 25px 25px 25px;
  border: none;
  background-color: rgba(179, 0, 255, 0.881);
  color:white;
}
::placeholder{
  padding-left: 10px;
  font-size: larger;
}
.SideBar{
  margin-top: 10px;
  width: 17%;
}
.Side-Bar-Component > img{
  width: 26px;
  height: 26px;
  margin-right:auto;
}
.Side-Bar-Component > span{
  margin-right: auto ;
}
.Side-Bar-Component{
  width:70%;
  margin-left:10%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.Side-Bar-Component > span{
  font-family: Arial, Helvetica, sans-serif;
}

.Side-Bar-Component:nth-child(1){
  background-color: rgb(128, 128, 128,0.2);
}
.Side-Bar-Component:hover{
  background-color: rgb(128, 128, 128,0.3);
  cursor: pointer;
}
.Main{
  display:grid;
  grid-template-columns: 17% 83%;
}
.SideBar{
  width: 100%;
  
}
.Subs{
  display: flex;
  flex-direction: row;
  margin-left: 0%;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
p{
  font-weight:600;
  padding: 3px 10px;
  border-radius: 7px;
  background-color: rgb(128, 128, 128,0.2);
}
.Subs > p:nth-child(1){
  background-color: black;
  color: white;
}
hr{
  border-top: 3px darkgray;
}
.Main{
  min-height: 80vh;
}
.videos{
  width: 80%;
  margin-left: 6%;
  margin-top: 2%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5%
}
.Single-vid{
  display: flex;
  flex-direction: column;
  width: 300px;
}
.Single-vid > img{
  border-radius: 30px;
  height: 200px;
  width: 300px;
}

.title{
  font-weight: bold;
  font-family:Arial, Helvetica, sans-serif ;
  font-size: larger;
  margin-left: 5%;
  margin-top: 2%;
}
.Name{
  margin-left: 15%;
  color: rgb(91, 91, 91);
}
.inline{
  margin-left:15% ;
  color: rgb(91, 91, 91);
}
.date{
  margin-left: 5%;
}
.Subscribed{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.element > img{
  width: 30px;
  height: 30px;
  border-radius: 30px;
  margin-right: auto;
}
.element{
  width:80%;
  margin-left:10%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  border-radius: 10px;
}
.element > span{
  margin-right: auto;
  font-family: 'Roboto', sans-serif;
}
.Subscribed > span{
  font-weight: bolder;
  font-family: 'Roboto', sans-serif;
  margin-right: auto;
  margin-left: 15%;
}
.element:hover{
  background-color: rgb(128, 128, 128,0.3);
  cursor: pointer;
}
.vid-info > img{
  
  margin: 3% 0;
  width: 30px;
  height: 30px;
  border-radius: 30px;
}
.vid-info{
  display: flex;
  align-items: center;
}
nav{
  margin: 0;
  padding: 0;
  background-color: white;
  position: fixed;
}
.SideBar{
  max-height: 100vh;
  max-width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.Secondary{
  max-height: 100%;
  overflow-y: scroll;
  scroll-behavior:smooth;
  
}
body{
  overflow: hidden;
}